body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 62.5%;
  font-family: Helvetica, Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#landingPage {
  min-height:100vh;
  width: 100%;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none;
          user-select: none;
}

.shapeObjectTitle {
  width: 60%;
}

.shapeObject {
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shapeObject:hover {
  cursor: pointer;
}

.clickable {
  font-size: 1rem;
}

.relative {
  position: relative;
  overflow: auto
}

.backButton {
  font-size: 1rem;
  padding: 1rem 2rem;
  border-radius: 1rem;
  background-color: rgba(255, 166, 0, 0.613);
  text-transform: uppercase;
  color: white;
  font-weight: bold;
  box-shadow: 0.2rem 0.2rem 1rem rgba(0, 0, 0, 1);
  text-align: center;
}


.backButton:hover {
  cursor: pointer;
}



.objectsContainer {
  width: 80%;
}
.column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 80vh;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
}

#desktopSite {
  display: none;
}


@media only screen and (max-width: 800px) {
  #mobileSite {
    display: none;
  }

  #desktopSite {
    display: flex;
    height: 100vh;
    width: 100%;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
  }
}


#leadingBall {
    height: 8rem;
    width: 8rem;
    background-color: black;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

/* #square {
    height: 10rem;
    width:  10rem;
    background-color: white
} */

.bdayGirlPicture {
    object-fit: cover;
    width: 100%;
    aspect-ratio: 10/15;
}

.bdayGirlPictureContainer {
    padding: 3rem 2rem;
    box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 1);
    background-color: white;
}

.videosContainer {
    background-color: black;
    position: absolute;
    width: 100%;
    min-height: 100vh;
    top: 0;
    left: 0;
    display: flex;
}

.outerBorder {
    width: 33.33%;
    padding: 2rem;
    display: flex;
}

.video {
    background-color: white;
    padding: 2rem 1rem;
    width: 100%;
}

#iLove {
    color: rgba(255, 0, 123, 0.577);
    border-radius: 10rem;
    text-decoration: underline;
    text-shadow: 0px 2px 10px rgba(256, 0, 0, 1);
    cursor: pointer;
}

#iLove:hover {
    text-shadow: none;
}
#bdayWishContainer {
    height: 80vh;
    padding: 2rem;
    background-color: white;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
img {
    max-height: 100%;
}

#wish {
    position: absolute;
    left: 50%;
    top: 70%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 2rem;
}

.col {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    min-height: 80vh;
    justify-content: space-between;
}
.alignRightExtreme {
    align-self: flex-end;
    margin-left: auto;
}

#scanText {
    background-color: black;
}
